/* First.css */
.first-body {
    font-family: 'Arial', sans-serif;
    color: #fff;
    margin-top: -32px;
    padding: 0;
}

.first-body.home {
    background-image: url('https://ia.eferrit.com/ia/fac1969a74192f02-1024x682.jpg'); /* Replace 'url_imaginii.jpg' with the correct path to your image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.7;
}

.text-container {
    text-align: center;
    margin-bottom: 20px;
}

.first-h1 {
    color: #3498db;
    font-size: 3em;
    margin-bottom: 15px;
    text-shadow: 20px 20px 24px rgba(0, 0, 0, 1);
}

.first-h3 {
    margin-left: 20px;
    margin-right: 20px;
    line-height: 1.6;
    font-size: 1.5rem;
    color: rgb(227, 227, 227);
    text-shadow: 2px 2px 4px black;
}
.first-button {
    background-color: #3498db;
    color: #fff;
    padding: 10px 20px;
    font-size: 1.2em;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-bottom: 20px;
    text-shadow: 16px 16px 20px rgba(0, 0, 0, 0.9);
}



.first-button:hover {
    background-color: #2980b9;
}

.image-container {
    text-align: center;
}

.first-img {
    max-width: 100%;
    height: auto;
    border-radius: 15px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 600px) {
    .first-h3 {
        margin-left: 10px;
        margin-right: 10px;
        font-size: 1.2rem;
    }
}