.homebody {
    margin: 0;
    padding: 0;
}

.homeimg {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    opacity: 0.7; 
}

.homeoverlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #f0f0f0; 
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 30.5); 
}

.homeh1 {
    font-size: 4em;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 20px;
}

.homeh2 {
    font-size: 2.5em;
    font-family: 'Roboto', sans-serif; 
    margin: 0;
}

.homeh3 {
    font-size: 1.5em;
    font-family: 'Roboto', sans-serif; 
    margin-top: 10px;
}

.homebutton {
    background-color: #4CAF50;
    color: white;
    padding: 15px 30px;
    font-size: 1.5em;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.homebutton:hover {
    background-color: #45a049;
}
